.connectpage{

   
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 1rem;
    box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;


}

.connectpage h2{

    color:  rgb(19 15 73);
    font-family: Poppins,sans-serif;
    font-size: 3.5rem;
    font-weight: 700;
    letter-spacing: -.05em;
    padding: 20px;
}

.contactmepage{

   padding: 3rem;
   
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
   
    margin-bottom: 3rem;
   
}


.contactmepage div{

    padding: 5px;
}

.contactmepage input{

   width: 25rem;
   padding: 10px;
   border-radius: 5px;
   border: none;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgb(209, 213, 219) 0px 0px 0px 1px inset;
  outline: none;
  font-size: 1em;
   
}

.contactmepage h3{
    font-family: 'PT Mono', monospace;
    padding: 3px;
    color:  rgb(19 15 73);
    font-weight: 700;
}

.contactmepage textarea{

    width: 25rem;
    height: 10rem;
    border-radius: 5px;
   border: none;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgb(209, 213, 219) 0px 0px 0px 1px inset;
  outline: none;
  padding: 10px;
  font-size: 1.2em;
}



.contactmepage button{

    font-family: 'PT Mono', monospace;
    border: none;
   margin-top: 0.5rem;
    padding: 15px 30px ;
   
    background-color:  rgb(19 15 73);
    color:white;
    border-radius: 10px;
    cursor: pointer;

}


@media  screen and (max-width:600px) {

    .connectpage{

       
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-bottom: 1rem;
        box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
    
    
    }

    .connectpage h2{

        color:  rgb(19 15 73);
        font-family: Poppins,sans-serif;
        font-size: 3rem;
        font-weight: 700;
        letter-spacing: -.05em;
        padding: 10px;
    }


    .contactmepage{

        
        
         box-shadow: none;
         display: flex;
         flex-direction: column;
         justify-content: center;
         align-items: center;
        
         margin-bottom: 3rem;
        
     }


     .contactmepage div{

        padding: 6px;
    }
     

    .contactmepage input{

        width: 19rem;
        padding: 10px;
        border-radius: 5px;
       border: 1px solid rgb(199, 193, 193);
      
       outline: none;
       font-size: 1em;
        
     }


     .contactmepage textarea{

        width: 19rem;
        padding: 10px;
        height: 10rem;
        border-radius: 5px;
        border: 1px solid rgb(199, 193, 193);
      
      outline: none;
      padding: 10px;
      font-size: 1.2em;
    }
    
    
}